<template>
    <div class="about">
        <div style="width: 5%; margin:0.5rem" v-for="(item, index) in svgkeys" :key="index">
            <svg-loader color="green" size="3rem" :icon="item"></svg-loader>
            <br>
            <span>{{item}}</span>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            svgkeys: [],
        };
    },

    props: {
        msg: String,
    },
    methods: {},
    mounted() {
        var tmp = require.context("@/res/svg", false, /\.svg$/).keys();
        tmp.forEach((item) => {
            this.svgkeys.push(item.match(/([%a-zA-Z0-9\-_]+)(\?.*)?/)[0]);
        });
    },
    unmounted() {},
};
</script>

<style scoped>
.about {
    display: flex;
    /*justify-content: space-around;*/
    align-items: center;
    flex-wrap: wrap;
    align-content: space-around;
}

</style>
